import React from "react";
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

function footer(){
    return (
    <div className="main-footer">
      <div class="container col-12 wd-100%  mt-5">
        <div class="row">
           
            <div class="col-sm 12">
                <p class="TituloFooter">Se inscreva para mais noticias</p>
                <form>
                  <div class="form-group">
                    <input style={{borderRadius:'18px'}} type="email" class="form-control" id="formGroupExampleInput" placeholder="Seu E-mail"/>
                  </div>
                </form>
            </div>
                <div className="col-sm 12">
                    <p class="TituloFooter">Go tokens</p>
                    <ul className="list-unstyled">                       
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Sobre</a></li>
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Explorar</a></li>
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Suporte</a></li>
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Educação</a></li>
                    </ul>
                </div>
                <div class="faixa" className="col-sm 12">
                <p class="TituloFooter">Comunidade</p>
                <ul className="list-unstyled">                       
                        <li ><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Fórum</a></li>
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Sugeridos</a></li>
                        <li><a style={{textDecoration:'none', color:'#C8C8C8'}} href="/">Tokens</a></li>
                    </ul>
                </div>
                <div class="col-sm 12">
                    <p class="TituloFooter">Idioma</p>
                    <a href="/"><button id="botaoFooter" style={{marginLeft:'0px'}}>Português BR </button></a>
                </div>
          <hr/>
          <div class="row">
            <p class="col-sm" style={{textAlign:"center", color:"#C8C8C8"}}>
              &copy;{new Date().getFullYear()} GoTokens | All rights reserved |
              Terms Of Service | Privacy
            </p>
          </div>
        </div>
      </div>
    </div>
    );
}

export default footer;