export default Faq;

function Tokenizaçao() {
	const [isOpen, setIsOpen] = useState(false);
	const toggle = () => setIsOpen(!isOpen);
	
	return (
		
		<section class="mb-4">
		  	<div className="FaqDiv" >
				
				<button className="faqbutton" onClick={toggle} style={{ marginBottom: "1rem"}}>
					<h5 style={{marginTop:'20px'}}>O que é tokenização?</h5>
				</button>
				<img onClick={toggle} style={{ cursor:'pointer'}} alt=""src={Vector}></img>
				
				<Collapse isOpen={isOpen}>
				<Card style={{boxShadow:'none', borderRadius:'20px', border:'none'}}>
					<CardBody >
					<p>É transformar um ativo físico em formato digital, através de um processo conhecido como “Tokenização”. O emissor dos tokens pode dividir um ativo em parcelas menores, aumentando sua liquidez ao poder movimentar frações do ativo. Já o detentor dos tokens conta com a segurança, transparência e autonomia conferida pela tecnologia utilizada na tokenização, a blockchain (banco de dados descentralizado). Por conta das características de funcionamento da Blockchain, os processos de transferência e o registro da posse de um token são simplificados.</p>
					
					<p>	Esta é uma tendência e já existem muitos ativos sendo negociados em forma de tokens. Um exemplo recente são os NFT - Non fungible tokens que representam obras de arte digitais. É possível tokenizar um imóvel, um título precatório e negociá-los.</p>

					<p>Uma outra prova de sucesso da estratégia de tokenização é a intenção dos Bancos Centrais e governos de tokenizar suas moedas fiduciárias. Isto já está em testes na China e o Banco Central do Brasil já sinalizou esta intenção também.</p>

					<p>A tokenização facilita a transferência e o registro da posse de um bem, por conta das características de funcionamento da blockchain.</p>
					
					</CardBody>					
				</Card>
				</Collapse>
			</div>		
		</section>		
	);
  }

  function OqEtoken() {
	const [isOpen, setIsOpen] = useState(false);
	const toggle = () => setIsOpen(!isOpen);
	
	return (
		
		<section class="mb-4">
		  	<div className="FaqDiv" >
				
				<button className="faqbutton" onClick={toggle} style={{ marginBottom: "1rem"}}>
					<h5 style={{marginTop:'20px'}}>Mas, o que é um token?</h5>
				</button>
				<img onClick={toggle} style={{ cursor:'pointer'}} alt=""src={Vector}></img>
				
				<Collapse isOpen={isOpen}>
				<Card style={{boxShadow:'none', borderRadius:'20px', border:'none'}}>
					<CardBody >
					<p>O token é um registro digital.</p>
					<p>É a representação digital de algo que existe no mundo real, ou seja o token representa um ativo base como por exemplo</p>
					<ul>
						<li><p>cotas de uma empresa, </p></li>
						<li><p>um imóvel,</p></li>
						<li><p>uma obra de arte, </p></li>
						<li><p>direitos de recebimentos futuros como valores a receber de um youtuber, </p></li>
						<li><p>pontos de fidelidade</p></li>
						<li><p>aluguéis, e muitos outros exemplos. </p></li>
					</ul>
					<p>Praticamente tudo que existe física e digitalmente e tem um valor, pode ser digitalizado na forma de um  token.</p>
					<p>Você pode criar um token para arrecadar doações com a vantagem de poder fazer isso pelo mundo todo.</p>
					
					</CardBody>					
				</Card>
				</Collapse>
			</div>		
		</section>		
	);
  }

