					<CardBody >
					<p>Como toda operação de compra e venda, estamos falando de uma relação entre comprador e vendedor. Você precisa entender o que está comprando. Porém ao comprar um token, você conta com a segurança, transparência e autonomia conferida pela tecnologia blockchain utilizada na tokenização. Por conta das características de funcionamento da Blockchain, os processos de aquisição, transferência e registro da posse de um token são transparentes e seguros. Uma grande vantagem das redes blockchain é que a posse do token é sua. Você mesmo pode custodiar o seu token e não depender de terceiros para prestar esse serviço. </p>					
					</CardBody>					
