				<Collapse isOpen={isOpen}>
				<Card style={{boxShadow:'none', borderRadius:'20px', border:'none'}}>
					<CardBody >
					<p>Wallet quer dizer Carteira.</p>

					<p>No mundo tradicional a carteira (aquela carteira de couro que você tem) é onde você guarda o seu dinheiro físico (as notas de Real). </p>

					<p>No mundo digital, uma wallet (digital) é onde ficam armazenados os seus tokens de forma on line. </p>

					<p>Existem as Hard Wallets que são dispositivos físicos que funcionam off line. Eles permitem quee seus tokens sejam guardados sem risco de sofrer ataques digitais.</p>

					<p>Em todos os casos você é responsável pela segurança das suas carteiras. Se você perder o acesso a elas não há o que fazer!</p>		
					</CardBody>					
				</Card>
				</Collapse>
